<template>
  <div id="app">
    <!-- 主体内容 -->
    <div class="body">
      <template>
        <el-table :data="tableData" style="width: 100%">
          <el-table-column
            prop="Name"
            label="部门"
            width="140"
          ></el-table-column>

          <el-table-column
            prop="0"
            label="部门报名"
            width="100"
          ></el-table-column>

          <el-table-column
            prop="1"
            label="部门未面试/线下报名"
            width="170"
          ></el-table-column>

          <el-table-column
            prop="2"
            label="部门未面试/线上报名"
            width="170"
          ></el-table-column>

          <el-table-column
            prop="3"
            label="未通过初试"
            width="100"
          ></el-table-column>

          <el-table-column
            prop="4"
            label="已通过初试"
            width="100"
          ></el-table-column>

          <el-table-column
            prop="5"
            label="未通过复试"
            width="100"
          ></el-table-column>

          <el-table-column prop="6" label="已通过复试"></el-table-column>
        </el-table>
      </template>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "database",
  props: {},
  components: {},
  data() {
    return {
      tableData: [],
    };
  },
 /*created() {
    this.showData();
  },*/
  methods: {
    out() {
      this.$router.push("/login");
    },
    showData() {
      var that = this;
      axios.post("http://127.0.0.1/api/count").then(
        (res) => {
          // console.log(res);
          that.tableData = res.data.data;
        },
        (err) => {
          console.log(err);
        }
      );

    },
  },
};
</script>

<style scoped>
#app {
  width: 1200px;
  margin: 0 auto;
}
.header {
  display: inline-block;
  margin-right: 725px;
}
.add-btn {
  margin-top: 20px;
  width: 100%;
}
.body {
  margin-top: 20px;
}
.search-input {
  margin-top: 20px;
}
.search-btn {
  margin-top: 20px;
  width: 50%;
  margin-left: 85px;
}
.hidden {
  display: none;
}
.el-input {
  width: 40%;
  margin-bottom: 45px;
}
el-table-column {
  text-align: center;
  border: solid 1px black;
}
</style>



